html {
  min-height: 100%;
  position: relative;
}

body {
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Verdana, sans-serif;
  background: white url(background.png) repeat fixed;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#header {
  position: fixed;
  top: 0; 
  left: 0;
  width: 100%;
  background-color: rgb(250, 243, 153);
}

#banner { 
  padding: 3px 25px;
  text-align: left;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: baseline;
}

#logo {
  width: 190px;
}

#tryYourHand {
  margin: 0px;
  color: rgb(247, 76, 46);
}

#navBar { 
  background-color: rgb(150, 150, 150); 
  width: 100%;
}

#navBarContent {
  text-align: left;
  display: flex;
  flex-direction: row;
  align-items: baseline;
}

button {
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Verdana, sans-serif;
  cursor: pointer;
}

#navBarButtons {
  margin-left: 22px;
}

.navBarButton {
  background-color: transparent;
  border-width: 2px;
  border-style: none none none solid;
  border-color: white;
  color: white;
  padding: 10px 20px;
  font-size: 12pt;
  cursor: pointer;
  float: left;
}

.navBarButton:after {
  content: "";
  clear: both;
  display: table;
}

.navBarButton:hover {
  background-color: rgb(27, 206, 230);
}

a:last-child button {
  border-right: solid 2px;
}

#navBarButtons .activeLink button {
  background-color: rgb(247, 76, 46);
}

#navBarButtons .activeLink button:hover {
  background-color: rgb(27, 206, 230);
}

.contentContainer {
  margin-top: 216px;
  margin-bottom: 80px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.mainContent {
  display: flex;
  flex-direction: column;
  align-items: center;
	width: 45%;
	border-radius: 12px;
}

#footer {
  position: fixed;
  bottom: 0; 
  left: 0;
  width: 100%;
  padding: 13px;
  background: rgb(250, 243, 153) url(border.png) repeat-x;
}

.styledButton {
  background-color: white;
  border: 2px solid rgb(114, 113, 113);
  color: rgb(114, 113, 113);
  padding: 12px;
  text-align: center;
  display: inline-block;
  font-size: 14px;
  font-weight: bold;
  margin: 18px 0px 18px 18px;
  border-radius: 4px;
  transition-duration: 0.4s;
}

.styledButton:hover {
  background-color: rgb(114, 113, 113);
  color: white;
}

input[type="text"]
{
  margin-top: 18px;
  font-size:16px;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Verdana, sans-serif;
  color: rgb(59, 59, 59);
  border-radius: 4px;
}

h1, h2, h3, h4, h5, h6
{
  color: rgb(100, 100, 100);
}